



















































































































































































































































































































































































































































































































































































































































































































































































































































































































































































import { Component, Vue } from "vue-property-decorator";

@Component
export default class Navigation extends Vue {
  cursorX: number = 146;
  cursorY: number = 95;

  /**
   * Event handler called when mouse is moved on computer screen
   * Used to calculate the position of the cursor
   */
  handleScreenMouseMove(event: MouseEvent) {
    const svgElement = this.$refs.svgElement as SVGSVGElement;
    const screen = this.$refs.screen as SVGGraphicsElement;

    const pt = svgElement.createSVGPoint();
    pt.x = event.clientX;
    pt.y = event.clientY;

    const ctm = screen.getScreenCTM();
    if (ctm) {
      const { x, y } = pt.matrixTransform(ctm.inverse());

      this.cursorX = x - 20 > 0 ? x - 20 : 0;
      this.cursorY = y - 20 > 0 ? y - 20 : 0;
    }
  }
}
